@font-face {
  font-family: 'Pixelar Regular W01 Regular';
  // line-height: 1.5rem;
  src: url('./fonts/PixelarW01Regular.woff2') format('woff2'),
    url('./fonts/PixelarW01Regular.woff') format('woff');
}

.rise-anim {
  transition: transform 0.5s ease;
}

.rise-anim:hover {
  transform: translate(0px, -20px);
}

.transition {
  transition: all 0.5s linear;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0 !important;
  // background: linear-gradient(180deg, hsla(216, 76%, 42%, 1) 0%, hsla(214, 41%, 71%, 1) 100%);
}

.day-sky {
  background: linear-gradient(180deg, hsla(216, 76%, 42%, 1) 0%, hsla(214, 41%, 71%, 1) 100%);
}

.night-sky {
  background: linear-gradient(180deg, hsla(243, 100%, 12%, 1) 0%, hsla(230, 32%, 38%, 1) 100%);
}

.dirt {
  background-color: #b87d4b;
}

.hidden-scrollbar::-webkit-scrollbar {
  display: none !important;
}

/* Hide scrollbar for IE, Edge and Firefox */
.hidden-scrollbar {
  -ms-overflow-style: none !important; /* IE and Edge */
  scrollbar-width: none !important; /* Firefox */
}

/* width */
.custom-scrollbar::-webkit-scrollbar {
  width: 8px;

}

/* Track */
// .custom-scrollbar::-webkit-scrollbar-track {
//   box-shadow: inset 0 0 5px grey;
//   border-radius: 5px;
// }

/* Handle */
.custom-scrollbar::-webkit-scrollbar-thumb {
  background: #e8e8e844;
  // background: gray !important;
  border-radius: 4px;
}

/* Handle on hover */
.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #e8e8e888;
}

