/**
 * ----------------------------------------
 * animation shadow-pop-tl
 * ----------------------------------------
 */

.shadow-pop-tl:hover {
  animation: shadow-pop-tl 0.3s cubic-bezier(0.47, 0, 0.745, 0.715) both;
}

@keyframes shadow-pop-tl {
  0% {
    box-shadow: 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e,
      0 0 #3e3e3e, 0 0 #3e3e3e;
    transform: translateX(0) translateY(0);
  }
  100% {
    box-shadow: -1px -1px #3e3e3e, -2px -2px #3e3e3e, -3px -3px #3e3e3e, -4px -4px #3e3e3e,
      -5px -5px #3e3e3e, -6px -6px #3e3e3e, -7px -7px #3e3e3e, -8px -8px #3e3e3e;
    transform: translateX(8px) translateY(8px);
  }
}

// My attempt at a box-shadow animation

.shadow-split {
  box-shadow: 0 0 #b514d3, 0 0 #f44336 !important;
  transition: box-shadow 500ms cubic-bezier(0.33, 1, 0.68, 1) !important;
  -webkit-transition: box-shadow 500ms cubic-bezier(0.33, 1, 0.68, 1) !important;
}

.shadow-split:hover {
  box-shadow: 6px 6px #b514d3, -6px -6px #f44336 !important;
}

.spin {
  -webkit-animation: spin 180s linear infinite;
  -moz-animation: spin 180s linear infinite;
  animation: spin 180s linear infinite;
}

@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

